import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";

export default function MediaCard({
  textPath,
  imgPath,
  ttPath,
  OPGGPath,
  LEARNPath
}) {
  return (
    <Card
      sx={{ maxWidth: 330, borderColor: "gray" }}
      /*color*/
      style={{ backgroundColor: "#333333" }}
    >
      <CardMedia component="img" alt="no image" height="180" image={imgPath} />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          {<span style={{ color: "white" }}>{textPath}</span>}
        </Typography>
        <Typography variant="body2" /*color="text.secondary"*/>
          {<span style={{ color: "silver" }}>{ttPath}</span>}
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small" href={OPGGPath}>
          OP.GG
        </Button>
        <Button size="small" href={LEARNPath}>
          {<span style={{ color: "green" }}> Learn More</span>}
        </Button>
      </CardActions>
    </Card>
  );
}
