import * as React from "react";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import Link from "@mui/material/Link";

import Footertext from "./Footertext.json";

function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="silver"
      /*color="text.secondary"*/
      align="center"
      {...props}
    >
      <Link color="inherit" href="https://www.daido-it.ac.jp/">
        大同大学
      </Link>
      {" D20107 "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

export default function Footer() {
  return (
    <Container
      maxWidth="md"
      component="footer"
      sx={{
        borderTop: (theme) => `1px solid ${theme.palette.divider}`,
        borderColor: "divider" /*borderColor: "gray"*/,
        mt: 3,
        py: [3, 6]
      }}
    >
      <Grid container spacing={4} justifyContent="space-evenly">
        {Footertext.map(({ footername, footerlink }) => (
          <Grid item xs={6} sm={3}>
            <ul>
              <Link
                variant="h5"
                color="#fff"
                /*color="inherit"*/ href={footerlink}
              >
                {footername}
              </Link>
            </ul>
          </Grid>
        ))}
      </Grid>

      <Copyright sx={{ mt: 5 }} />
    </Container>
  );
}
