import * as React from "react";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";

export default function MenuTop() {
  return (
    <Container
      disableGutters
      maxWidth="sm"
      component="main"
      sx={{ pt: 1, pb: 2 }}
    >
      <Typography
        variant="h5"
        align="center"
        /*color="text.secondary"*/ color="white"
        component="p"
      >
        CHOOSE YOUR
      </Typography>
      <Typography
        component="h1"
        variant="h2"
        align="center"
        color="white"
        /*color="text.primary"*/
        gutterBottom
      >
        CHAMPION
      </Typography>
      <Typography
        variant="h5"
        align="center"
        /*color="text.secondary"*/ color="silver"
        component="p"
      >
        With more than 140 champions, you’ll find the perfect match for your
        playstyle. Master one, or master them all.
      </Typography>
    </Container>
  );
}
